            </div>

            <!-- Actions -->
            <div class="flex flex-wrap gap-3">
              <cc-button
                type="primary"
                :title="t('btn.encode')"
                icon="https://unpkg.com/lucide-static@latest/icons/lock.svg"
                :alt="t('btn.encode')"
                @click="generateMd5"
              />

              <cc-button
                :title="t('btn.clear')"
                icon="https://unpkg.com/lucide-static@latest/icons/eraser.svg"
                :alt="t('btn.clear')"
                @click="inputText = ''"
              />

              <cc-button
                :title="t('btn.copy')"
                icon="https://unpkg.com/lucide-static@latest/icons/copy.svg"
                :alt="t('btn.copy')"
                @click="copyToClipboard(inputText, t)"
              />

              <cc-button
                :title="t('btn.paste')"
                icon="https://unpkg.com/lucide-static@latest/icons/clipboard-paste.svg"
                :alt="t('btn.paste')"
                @click="onPaste"
