<script setup>
import { ref } from "vue";
import CryptoJS from "crypto-js";
import { copyToClipboard, clipboardText } from "#build/imports";
import Tools from "./components/tools.vue";
import Output from "./components/output.vue";
import History from "./components/history.vue";
import Description from "./components/description.vue";

const { t } = useI18n({
  useScope: "local",
});

const inputText = ref("");
const results = ref([]);
const result = ref("");
const bitLength = ref(16);
const uppercase = ref(false);
const historyRef = ref(null); // 添加对History组件的引用

const generateMd5 = () => {
  // 使用 CryptoJS 生成 MD5 编码, 包括 16 位和 32 位的大小写
  const md5 = CryptoJS.MD5(inputText.value).toString();

  if (bitLength.value == 16) {
    result.value = uppercase.value
      ? md5.substr(8, 16).toUpperCase()
      : md5.substr(8, 16);
  } else {
    result.value = uppercase.value ? md5.toUpperCase() : md5;
  }

  // 保存到历史记录
  if (inputText.value.trim()) {
    const historyItem = {
      bit: bitLength.value,
      str: inputText.value,
      upper: uppercase.value,
      result: result.value
    };
    // 使用ref引用调用history组件的save方法
    if (historyRef.value?.save) {
      historyRef.value.save(historyItem);
    }
  }
};

const onPaste = async () => {
  inputText.value = await clipboardText();
};

// 处理历史记录点击事件
const recordClick = (record) => {
  if (record) {
    // 从记录中恢复状态
    inputText.value = record.str || "";
    bitLength.value = record.bit || 32;
    uppercase.value = !!record.upper;

    // 重新生成MD5或直接使用保存的结果

    generateMd5();
  }
};

// 设置 SEO
import { setupSeoMeta } from "~/utils/seo";
setupSeoMeta(useSeoMeta, t);
</script>

