<template>
  <div
    class="mt-6 bg-white rounded-xl shadow-lg backdrop-blur-lg bg-opacity-90 p-6 border border-slate-200"
  >
    <div class="flex items-center justify-between mb-4">
      <div class="flex items-center">
        <img
          src="https://unpkg.com/lucide-static@latest/icons/history.svg"
          alt="历史记录"
          class="w-5 h-5 text-primary-600"
        />
        <h2 class="text-xl font-semibold ml-2">{{ t('title') }}</h2>
      </div>
      <button
        class="text-xs text-slate-500 hover:text-slate-700 flex items-center"
        @click="clearHistory"
      >
        <img
          src="https://unpkg.com/lucide-static@latest/icons/trash-2.svg"
          alt="清空历史"
          class="w-3.5 h-3.5 mr-1"
        />
        {{ t('btn.clear') }}
      </button>
    </div>

    <div class="space-y-2 max-h-[180px] overflow-y-auto" id="history-container">
      <!-- Empty state for history -->
      <div v-if="historyRecords.length === 0" class="text-center py-4 text-sm text-slate-400">
        <img
          src="https://unpkg.com/lucide-static@latest/icons/clock.svg"
          alt="暂无历史"
          class="w-6 h-6 mx-auto mb-2 opacity-40"
        />
        {{ t('tips') }}
      </div>

      <!-- History items -->
      <div 
        v-for="(record, index) in historyRecords" 
        :key="index"
        class="p-3 border border-slate-200 rounded-lg hover:bg-slate-50 cursor-pointer"
        @click="handleRecordClick(record)"
      >
        <div class="flex items-center justify-between">
          <div class="text-sm font-medium truncate flex-1">{{ record.str }}</div>
          <div class="flex items-center gap-1 ml-2">
            <span class="px-1.5 py-0.5 text-xs rounded bg-blue-100 text-blue-800 font-medium">
              {{ record.upper ? 'U' : 'L' }}
            </span>
            <span class="px-1.5 py-0.5 text-xs rounded bg-green-100 text-green-800 font-medium">
              {{ record.bit === 32 ? '32' : '16' }}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
const { t } = useI18n({
  useScope: "local",
});

const STORAGE_KEY = 'md5-history';
const MAX_HISTORY = 20;

const historyRecords = ref([]);
const emit = defineEmits(['record-click']);

// Load history from localStorage on component mount
onMounted(() => {
  loadHistory();
});

// Load history from localStorage
const loadHistory = () => {
  try {
    const savedHistory = localStorage.getItem(STORAGE_KEY);
    if (savedHistory) {
      historyRecords.value = JSON.parse(savedHistory);
    }
  } catch (error) {
    console.error('Failed to load history:', error);
    historyRecords.value = [];
  }
};

// Save record to history
const save = (record) => {
  // Make sure record has the required fields
  if (!record || typeof record.str !== 'string' || typeof record.bit !== 'number') {
    console.error('Invalid record format:', record);
    return;
  }
  
  // Ensure the record has the upper field (default to false if not provided)
  if (record.upper === undefined) {
    record.upper = false;
  }
  
  // Check if a duplicate record already exists
  const duplicateIndex = historyRecords.value.findIndex(item => 
    item.str === record.str && 
    item.bit === record.bit && 
    item.upper === record.upper
  );
  
  // If duplicate exists, remove it
  if (duplicateIndex !== -1) {
    historyRecords.value.splice(duplicateIndex, 1);
  }
  
  // Add record to the beginning of the array (newest first)
  historyRecords.value.unshift(record);
  
  // Limit the number of records to MAX_HISTORY
  if (historyRecords.value.length > MAX_HISTORY) {
    historyRecords.value = historyRecords.value.slice(0, MAX_HISTORY);
  }
  
  // Save to localStorage
  saveToLocalStorage();
};

// Save history to localStorage
const saveToLocalStorage = () => {
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(historyRecords.value));
  } catch (error) {
    console.error('Failed to save history:', error);
  }
};

// Clear history
const clearHistory = () => {
  historyRecords.value = [];
  saveToLocalStorage();
};

// Handle click on history record
const handleRecordClick = (record) => {
  emit('record-click', record);
};

// Expose save function to parent components
defineExpose({
  save
});
</script>

<i18n lang="json">
    {
      "en": {
        "title": "Encryption History",
        "tips": "No encryption history yet",
        "btn": {
          "clear": "Clear"
        }
      },
      "zh": {
        "title": "加密历史",
        "tips": "暂无加密历史记录",
        "btn": {
          "clear": "清空"
        }
      }
    }
</i18n>